export default {
    /**
     * ID
     * @param {pageidID,version,schemaType:01} params 
     * @returns 
     */
    fetchPageSettingDataForVersion(params){
        params.savetype = IDM.setting.pageSchema.saveType;
        //json
        if(IDM.setting.pageSchema.saveType=="disk"&&params.version&&process.env.NODE_ENV =="production"){
            return IDM.http.get(IDM.setting.pageSchema.jsonDir.replace(/{projectNo}/g, params.projectNo||IDM.setting.webRoot.projectNo).replace(/{pageid}/g,params.pageid).replace(/{version}/g,params.version));
        }
        return IDM.http.get(IDM.setting.api.pageSettingDataUrl||IDM.setting.mockurl.pageSettingDataUrl,params);
    },
    /**
     * 
     * @param {*} url 
     * @param {pageidID} params 
     * @returns 
     */
    fetchApplicationInfo(url,params){
        return IDM.http.get(url,params);
    },
    /**
     * 
     * @param {*} url 
     * @param {pageidID} params 
     * @returns 
     */
    fetchUserInfo(url,params){
        return IDM.http.get(url,params);
    },
    /**
     * 
     * @param {*} url 
     * @param {pageidID} params 
     * @returns 
     */
    fetchUserCustomizationInfo(url,params){
        return IDM.http.get(url,params);
    },
    /**
     * 
     * @param {*} url 
     * @param {pageidID} params 
     * @returns 
     */
    fetchThemeInfo(url,params){
        return IDM.http.get(url,params);
    },
    /**
     * get
     * @param {*} url 
     * @param {*} params 
     */
    getPageInterfaceInfo(url,params){
        return IDM.http.get(url,params);
    },
    /**
     * post
     * @param {*} url 
     * @param {*} params 
     * @param {*} options 
     * @returns 
     */
    postPageInterfaceInfo(url,params,options){
        return IDM.http.post(url,params,options);
    }
}