export default {
    props: {
    },
    data() {
        return {
        }
    },
    watch: {
    },
    methods: {
      /**
       * 
       */
      themeInterfaceHandle(resultData,echoDataName){
        let that = this;
        let propData = this.PageSettingData.page.layout.props.compositeAttr;
        let themeDataFiled = propData.themeDataFiled,
        themeDataFunction = propData.themeDataFunction,
        switchThemeValue=resultData;
        if(themeDataFiled){
          let filedExp = themeDataFiled;
          var dataObject = {IDM:window.IDM};
          dataObject[echoDataName] = resultData;
  
          if(filedExp){
            filedExp = echoDataName+(filedExp.startsWiths("[")?"":".")+filedExp;
            switchThemeValue = window.IDM.express.replace.call(this, "@["+filedExp+"]", dataObject);
          }
        }
        if(themeDataFunction&&themeDataFunction.length>0){
          var paramObject = {
                ...that.commonParam(),
                _this:that
              };
            paramObject[echoDataName] = resultData;
          themeDataFunction.forEach(sitem=>{
            try {
              switchThemeValue = window[sitem.name]&&window[sitem.name].call(this,{customParam:sitem.param,...paramObject});
            } catch (error) {
            }
          })
        }
        that.mainPage&&IDM.theme.setCurrentThemeInfo(switchThemeValue);
        that.themeSetObject = switchThemeValue;
      },
        /**
         * 
         */
        setCurrentPageTitle(islater){
            //
            document.title = IDM.express.replace(this.PageSettingData.pageBaseInfo.title || IDM.setting.applications.defaultTitle,{IDM:IDM,window,...this.pageInterfaceResultObject});
            //pageDynamicHeadMetaConfig
            if(this.mainPage&&islater&&IDM.setting.applications.pageDynamicHeadMetaConfig&&IDM.setting.applications.pageDynamicHeadMetaConfig.length){
              try {
                for (let index = 0; index < IDM.setting.applications.pageDynamicHeadMetaConfig.length; index++) {
                  const item = IDM.setting.applications.pageDynamicHeadMetaConfig[index];
                  var meta = document.createElement("meta");
                  Object.keys(item).forEach(key=>{
                    let value = IDM.express.replace(item[key],{IDM:IDM,window,...this.pageInterfaceResultObject});
                    meta.setAttribute(key,value);
                  })
                  document.head.appendChild(meta);
                }
              } catch (error) {
                console.error(error);
              }
            }
        },
        /**
         * svg
         * @param {*} moduleDom 
         * @param {*} comObject 
         */
        svgComponentPropsHandle(moduleDom,comObject){
          let that = this;
          let propData = comObject&&comObject.props.compositeAttr?comObject.props.compositeAttr:{};
          moduleDom.attr("title",propData.title);
          //
          moduleDom.bind("click", function (e) {
            that.commonCustomEventFunHandle(propData,"clickFunction");
          })
        },
        /**
         * 
         * @param {*} propData 
         * @param {*} name 
         * @param {*} paramObject 
         * @returns 
         */
        commonCustomEventFunHandle(propData,name,paramObject){
          let that = this;
          propData = propData||this.pagePropData;
          var customHandle = propData[name],resultData;
          let paramData = {
            ...that.commonParam(),
            _this: that,
          };
          if(paramObject){
            paramData={
              ...paramData,
              ...paramObject
            }
          }
          customHandle &&
            customHandle.forEach((item) => {
              resultData = window[item.name] &&
                window[item.name].call(this, {
                  ...paramData,
                  customParam: item.param
                });
            });
          return resultData
        },
        /**
         * url
         * url
         */
        commonParam(){
          let urlObject = IDM.url.queryObject();
          var params = {
            pageId:
              window.IDM.broadcast && window.IDM.broadcast.pageModule
                ? window.IDM.broadcast.pageModule.id
                : "",
            urlData: JSON.stringify(urlObject),
          };
          return params;
        },
        /**
         * 
         * @param {
         *  type:"typelinkageResultlinkageDemandlinkageReload
         * linkageOpenDialoglinkageCloseDialoglinkageShowModulelinkageHideModulelinkageResetDefaultValue"
         *  message:{},
         *  rangeModule:"packageid this.$root.propData.compositeAttr["attrKey"]attrKeybindKey,['1','2']"",
         *  className:""
         *  globalSend:truerangeModuleclassNamefalse
         * } object 
         */
        sendBroadcastMessage(object,routerId){
            window.IDM.broadcast&&window.IDM.broadcast.send(object,routerId||"");
        },
        /**
         * 
         */
        deleteLayoutAttachAttr(module) {
          if (!(module instanceof Array)) {
            module = [module];
          }
          module.forEach((item) => {
            delete item.env;
            delete item.hasLoading;
            delete item.loaded;
            delete item.loading;
            delete item.moduleReload;
            delete item.mountComplete;
            delete item.props;
            delete item.routerId;
            if (item.children && item.children.length > 0) {
              this.deleteLayoutAttachAttr(item.children);
            }
          });
        }
    },
    mounted() {
    },
}
