        userDefinedDomList = document.querySelector(`#router_page_${that.routerId}`).querySelectorAll(".idm_module_user_defined");
      }:null;
      //Dom
      userDefinedContainerDomList = document.querySelector(`#router_page_${that.routerId}`).querySelectorAll(".drag_container[idm-ctrl-id='"+customModuleObject.id+"']");
      that.registerModuleMouseDownEvent();
      if(save&&this.pageUserDefinedStatus===false){
        this.userDefinedSave();
