    /**
     * 
     */
    closeThisOpenDialog(ctx){
      var customParam = ctx.customParam?ctx.customParam:ctx;
      var moduleIdArray = [];
      customParam.targetModule.forEach(item=>{
        moduleIdArray.push(item.moduleId);
        this.dialogObjectList.forEach((ditem,dindex)=>{
          if(ditem.packageid==item.moduleId){
            ditem.dialogIsHideing = true;
            $("#router_page_"+this.routerId+" .idm_dialog_shade_layer_"+item.moduleId).attr("hideing","hideing");
            if(ditem.layerOption){
              ditem.layerOption.cancel&&ditem.layerOption.cancel(ditem);
            }
            //yes
            if(ctx.isOkEvent){
              ditem.layerOption&&ditem.layerOption.yes&&ditem.layerOption.yes(ditem);
            }
          }
        })
      });
      if(this.dialogObjectList.length<=moduleIdArray.length){
        this.dialogIsHideing = true;
      }
      let that = this;
      that.sendBroadcastMessage({
          type:"linkageCloseDialog",
          message:ctx,
          rangeModule:moduleIdArray
        })
      setTimeout(() => {
        customParam.targetModule.forEach(item=>{
          $("#router_page_"+this.routerId+" #"+item.moduleId).css("display","none");
          that.destroyDialog(item.moduleId);
          //dialogObjectList
          if(that.dialogObjectList.length==1){
            that.dialogObjectList = [];
            return;
          }
          let existsIndex = -1;
          that.dialogObjectList.forEach((ditem,dindex)=>{
            if(ditem.packageid==item.moduleId){
              existsIndex = dindex;
              if(ditem.layerOption){
                ditem.layerOption.end&&ditem.layerOption.end(ditem);
              }
            }
          })
          if (existsIndex > -1) {
            //
            that.dialogObjectList.splice(existsIndex, 1);
          }
        });
        if(that.dialogObjectList.length==0){
          that.dialogVisibleStatus = false;
          that.dialogIsHideing = false;
        }
      }, 500);
      if(ctx.callback){
        ctx.callback();
      }
    },
