        if (!item[loadType]&&item.nocontainer!==true) {
          isLoaded = false;
          // if(isnotloadinner&&(item.containerIndex||item.containerIndex=="0")){
          //   isLoaded = true;
          // }
        }
      });
      if (isLoaded) {
        moduleData.forEach((item) => {
          if(item.comType=="dialog"&&!item.renderLicence){
            //false
            return;
          }
          if (item.children && item.children.length > 0 && isLoaded) {
            isLoaded = this.pageModuleDataIsLoaded(
              item.children,
              isnotloadinner,
              loadType
            );
          }
        });
      }
      return isLoaded;
    },
    /**
     * dom
     */
    createModuleDom(comObject) {
      let that = this;
      //ID
      comObject.packageid = comObject.packageid || "component_" + IDM.UUID();
      //ID
      comObject.id = comObject.id || comObject.packageid + "_inner";
      //div
      var newComDiv = $("<div />");
      //
      newComDiv.attr("id", comObject.packageid);
      newComDiv.attr("idm-com-anchor-id", comObject.packageid);
      newComDiv.attr("idm-com-id", comObject.comId);
      newComDiv.attr("idm-com-classid", comObject.classId);
      newComDiv.attr("idm-com-classname", comObject.className);
      if(comObject.loadAndHide){
        newComDiv.attr("style","display:none");
      }
      //
      newComDiv.addClass("idm_module");
      newComDiv.addClass(comObject.packageid);
      let innerDom = $("<div />").attr("id", "idm_"+comObject.id+this.routerId);
      //svg
      if(comObject.comType=="svg"&&comObject.svgData){
        innerDom.append(comObject.svgData);
        //
        that.svgComponentPropsHandle(innerDom,comObject);
      }
      //divID
      newComDiv.append(innerDom);
      if(comObject.positionMode=="absolute"){
        that.createHoverLayoutHandle(newComDiv,comObject)
      }
      return newComDiv;
    },
    /**
      * dom
      */
    createHoverLayoutHandle(newComDiv, comObject) {
        let that = this;
        // 
        newComDiv.addClass("idm_module_absolute");
        this.resetHoverModuleStyleInfo(newComDiv,comObject);
        //svg
    },
    /**
      * 
      * @param {*} moduleDOM 
      * @param {*} currentModule 
      */
    resetHoverModuleStyleInfo(moduleDOM,currentModule){
        let that = this;
        let _basicAttr = currentModule.props && currentModule.props.basicAttr?currentModule.props && currentModule.props.basicAttr:{};
        //
        const modulePositionOrigin = _basicAttr.positionOrigin || "leftTop";
        let moduleCurrentWH = _basicAttr.positionWH || `${that.defaultModuleSize.width}.${that.defaultModuleSize.height}`;
        moduleCurrentWH = moduleCurrentWH.split(",");
        let moduleCurrentW = moduleCurrentWH.length > 0 ? parseFloat(moduleCurrentWH[0]) : 0,
            moduleCurrentH = moduleCurrentWH.length > 1 ? parseFloat(moduleCurrentWH[1]) : 0;
        let moduleCurrentXY = _basicAttr.positionXY || "0,0";
        moduleCurrentXY = moduleCurrentXY.split(",");
        let moduleCurrentX = moduleCurrentXY.length > 0 ? parseFloat(moduleCurrentXY[0]) : 0,
            moduleCurrentY = moduleCurrentXY.length > 1 ? parseFloat(moduleCurrentXY[1]) : 0;
        
        //
        let moduleZindex = _basicAttr.zIndex || 0;
        let moduleOpacity = _basicAttr.opacity==0?0:(_basicAttr.opacity||100);
        let moduleScale = _basicAttr.scale||1;
        let moduleRotateX = _basicAttr.rotateX||0;
        let moduleRotateY = _basicAttr.rotateY||0;
        let moduleRotateZ = _basicAttr.rotateZ||0;


        let styleObject = {};
        switch (modulePositionOrigin) {
            case "leftTop":
                styleObject = {
                    left: moduleCurrentX + "px",
                    top: moduleCurrentY + "px",
                    right: "auto",
                    bottom: "auto",
                    width: moduleCurrentW + "px",
                    height: moduleCurrentH + "px"
                }
                break;
            case "rightTop":
                styleObject = {
                    left: "auto",
                    top: moduleCurrentY + "px",
                    right: moduleCurrentX + "px",
                    bottom: "auto",
                    width: moduleCurrentW + "px",
                    height: moduleCurrentH + "px"
                }
                break;
            case "rightBottom":
                styleObject = {
                    left: "auto",
                    top: "auto",
                    right: moduleCurrentX + "px",
                    bottom: moduleCurrentY + "px",
                    width: moduleCurrentW + "px",
                    height: moduleCurrentH + "px"
                }
                break;
            case "leftBottom":
                styleObject = {
                    left: moduleCurrentX + "px",
                    top: "auto",
                    right: "auto",
                    bottom: moduleCurrentY + "px",
                    width: moduleCurrentW + "px",
                    height: moduleCurrentH + "px"
                }
                break;
        }
        styleObject["z-index"] = moduleZindex;
        styleObject["opacity"] = moduleOpacity/100;
        styleObject["transform"] = `scale(${moduleScale}) rotateX(${moduleRotateX}deg) rotateY(${moduleRotateY}deg) rotateZ(${moduleRotateZ}deg)`;
        moduleDOM && moduleDOM.css(styleObject);
    },
    /**
     * 
     */
    getModuleTree(module, packageid) {
      if (!(module instanceof Array)) {
        module = [module];
      }
      var _findModule;
      module.forEach((item) => {
        if (item.packageid == packageid) {
          _findModule = item;
        } else if (!_findModule && item.children) {
          _findModule = this.getModuleTree(item.children, packageid);
        }
      });
      return _findModule;
    },
    /**
     * 
     */
    moduleReload(packageid,innerContainerIndex){
      //innerContainerIndex
      const reloadModule = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        packageid
      );
      let reloadModuleArray = [reloadModule];
      let parentModule;
      if(innerContainerIndex||innerContainerIndex=="0"){
        //
        reloadModuleArray = [];
        reloadModule&&reloadModule.children&&reloadModule.children.forEach(item=>{
          if(item.containerIndex==innerContainerIndex){
            reloadModuleArray.push(item);
          }
        })
        parentModule = reloadModule;
      }else{
        //
        let globalIdArray = reloadModule.globalId.split(".");
        const globalId = globalIdArray.length<=2?globalIdArray[0]:globalIdArray[globalIdArray.length-2];
        parentModule = this.getModuleTree(
          this.PageSettingData.page.layout.children,
          globalId
        );
      }
      //
      this.resetLayoutInnerModuleLoadedStatus(reloadModuleArray);
      reloadModuleArray&&reloadModuleArray.forEach((item) => {
        $("#router_page_"+this.routerId+" #" + item.packageid).remove();
      });
      
      //
      this.initModuleTreeToPage(
        reloadModuleArray,
        true,
        parentModule,
        false
      );
    },
    /**
     * 
     */
    moduleMountComplete(module) {
      let that = this;
      //map
      this.PageSettingData.page.componentsMap.forEach((item) => {
        if (item.packageid == module.packageid) {
          item.idmBroadcastMessage = module.idmBroadcastMessage;
          item.idmSetContextValue = module.idmSetContextValue;
          item.idmGetContextValue = module.idmGetContextValue;
        }
      });
      console.log("", module);
      let opModule = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        module.packageid
      );
      //
      opModule.loading = false;
      //
      opModule.loaded = true;
      if (opModule.children && opModule.children.length > 0) {
        //inner
        const targetNode = $("#" + module.packageid);
        if (targetNode.find(":visible").length == 0) {
          //
          var invcount = 0;
          var intval = setInterval(function () {
            invcount++;
            if (
              $("#router_page_"+that.routerId+" #" + module.packageid).find(":visible").length == 0 &&
              invcount <= 20
            ) {
              return;
            }
            clearInterval(intval);
            that.moduleMountCompleteCommon(module);
          }, 5);
        } else {
          //3
          setTimeout(() => {
            that.moduleMountCompleteCommon(module);
          }, 3);
        }
      } else {
          //3
          setTimeout(() => {
            that.moduleMountCompleteCommon(module);
          }, 3);
      }
      // this.initModuleInnerEventHandle(module);
      // class
      // drag_container_empty
      // drag_container
      // idclass
      // idm_module
      // drag_container_inlineblock: 

      //  
      // idm-com-anchor-idID
      // id:ID
      // idm-com-idID
      // idm-com-classid
      // idm-com-classname
      // idm-container-index
    },
    moduleMountCompleteCommon(module){
      let that = this;
      var moduleDom = $("#router_page_"+this.routerId+" #" + module.packageid + "");
      var moduleInnerDom = moduleDom.find(">*[idm-ctrl]");
      var idmCtrl = moduleInnerDom.attr("idm-ctrl"),
        idmCtrlId = moduleInnerDom.attr("idm-ctrl-id"),
        domId = moduleInnerDom.attr("id");
      if (!domId) {
        moduleInnerDom.attr("id", idmCtrlId);
      }
      switch (idmCtrl) {
        case "drag_container": //
          moduleDom.addClass("drag_container");
          break;
        case "drag_container_inlineblock": //
          moduleDom.addClass("drag_container drag_container_inlineblock");
          break;
        case "idm_module": //
          break;
      }

      //
      if (!this.pageModuleDataIsLoaded(null, true)) {
        this.$nextTick(function () {
          this.initModuleTreeToPage(
            null,
            true,
            this.PageSettingData.page.layout
          );
        });
      }else if (this.pageModuleDataIsLoaded(null, true,"loaded")){
        this.requestPageCommonInterfaceData();
      }
    },
    /**
     * 
     * 
     * module{packageid:idid}
     */
    initModuleInnerEventHandle(module) {
      let that = this;
      //
      var moduleDom = $("#router_page_"+that.routerId+" #" + module.packageid + "");
      var moduleInnerDom = moduleDom.find(
        ".drag_container[idm-ctrl-id='" + module.id + "'][idm-ctrl-inner]"
      );
      if (moduleInnerDom.length > 0) {
        //
        const opModule = this.getModuleTree(
          this.PageSettingData.page.layout.children,
          module.packageid
        );
        this.resetLayoutInnerModuleLoadedStatus(opModule.children);
        if (opModule.children && opModule.children.length > 0) {
          opModule.children.forEach((item) => {
            $("#" + item.packageid).remove();
          });
          this.initModuleTreeToPage(opModule.children, true, opModule);
        }
      }
    },
    /**
     * 
     * @param {*} module 
     * @param {*} resetModuleIdList
     */
    resetLayoutInnerModuleLoadedStatus(module,resetModuleIdList) {
      if (!(module instanceof Array)) {
        module = [module];
      }
      module.forEach((item) => {
        let hasFindLoadId = (resetModuleIdList&&resetModuleIdList.indexOf(item.packageid)>-1);
        let childrenResetModuleIdList = resetModuleIdList;
        if(!resetModuleIdList||hasFindLoadId){
          //
          delete item.loaded;
          //...
          delete item.loading;
          //
          delete item.hasLoading;
          if(hasFindLoadId){
            item.againLoad = true;
            childrenResetModuleIdList = null
          }
        }
        if (item.children && item.children.length > 0) {
          this.resetLayoutInnerModuleLoadedStatus(item.children,childrenResetModuleIdList);
        }
      });
    },


    addModuleEndpoint(module){
        var common = {
            isSource: true,
            isTarget: true,
            endpoint: "Dot"
        }
        let _basicAttr = module.props && module.props.basicAttr ? module.props && module.props.basicAttr : {};
        let offsetsTop = [0,0],
        offsetsTopRight = [0,0],
        offsetsRight = [0,0],
        offsetsBottomRight = [0,0],
        offsetsBottom = [0,0],
        offsetsBottomLeft = [0,0],
        offsetsLeft = [0,0],
        offsetsTopLeft = [0,0];
        if(_basicAttr.AnchorsTopLeft){
          offsetsTop = _basicAttr.AnchorsTop.split(",");
          offsetsTopRight = _basicAttr.AnchorsTopRight.split(",");
          offsetsRight = _basicAttr.AnchorsRight.split(",");
          offsetsBottomRight = _basicAttr.AnchorsBottomRight.split(",");
          offsetsBottom = _basicAttr.AnchorsBottom.split(",");
          offsetsBottomLeft = _basicAttr.AnchorsBottomLeft.split(",");
          offsetsLeft = _basicAttr.AnchorsLeft.split(",");
          offsetsTopLeft = _basicAttr.AnchorsTopLeft.split(",");
        }
        debugger
        //8
        let endpointList = [
            { anchor: [0.5,0,0,-1,parseFloat(offsetsTop[0]),parseFloat(offsetsTop[1])], cssClass: module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-top" },
            { anchor: [1,0,0,-1,parseFloat(offsetsTopRight[0]),parseFloat(offsetsTopRight[1])], cssClass:module.packageid + "_endpoint idm_flow_endpoint " +  "endpoint-top-right" },
            { anchor: [1,0.5,1,0,parseFloat(offsetsRight[0]),parseFloat(offsetsRight[1])], cssClass: module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-right" },
            { anchor: [1,1,0,1,parseFloat(offsetsBottomRight[0]),parseFloat(offsetsBottomRight[1])], cssClass: module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-bottom-right" },
            { anchor: [0.5,1,0,1,parseFloat(offsetsBottom[0]),parseFloat(offsetsBottom[1])], cssClass: module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-bottom" },
            { anchor: [0,1,0,1,parseFloat(offsetsBottomLeft[0]),parseFloat(offsetsBottomLeft[1])], cssClass: module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-bottom-left" },
            { anchor: [0,0.5,-1,0,parseFloat(offsetsLeft[0]),parseFloat(offsetsLeft[1])], cssClass: module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-left" },
            { anchor: [0,0,0,-1,parseFloat(offsetsTopLeft[0]),parseFloat(offsetsTopLeft[1])], cssClass: module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-top-left" },
            // { anchor: "Center", cssClass: module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-center" },
        ]
        this.plumbIns.addEndpoints(module.packageid, endpointList, common)
    },
    /**
      * 
      * @param {*} linkObject 
      */
    linkConnectionStyleChange(_linkObject){
      let that = this;
        const linkFilterList = this.PageSettingData.page.linkList.filter(item=>item.id==_linkObject.id);
        if(!linkFilterList.length){
            return
        }
        const linkObject = linkFilterList[0];
        let linkPropData = linkObject.propData;
        this.plumbIns.select().each((connection) => {
            if(connection.id==linkObject.connId){
                if(linkPropData.linkType){
                    let connectorParam = [
                        linkPropData.linkType
                    ]
                    switch (linkPropData.linkType) {
                        case "Bezier":
                            connectorParam[1]={
                                curviness:linkPropData.BezierCurviness||linkPropData.BezierCurviness==0?linkPropData.BezierCurviness:150
                            }
                            break;
                        case "Straight":
                            connectorParam[1]={
                                stub:linkPropData.StraightStub||linkPropData.StraightStub==0?linkPropData.StraightStub:0,
                                gap:linkPropData.StraightGap||linkPropData.StraightGap==0?linkPropData.StraightGap:0
                            }
                            break;
                        case "Flowchart":
                            connectorParam[1]={
                                stub:linkPropData.FlowchartStub||linkPropData.FlowchartStub==0?linkPropData.FlowchartStub:30,
                                gap:linkPropData.FlowchartGap||linkPropData.FlowchartGap==0?linkPropData.FlowchartGap:2,
                                alwaysRespectStubs:true,
                                cornerRadius:linkPropData.FlowchartCornerRadius||linkPropData.FlowchartCornerRadius==0?linkPropData.FlowchartCornerRadius:8,
                                midpoint:linkPropData.FlowchartMidpoint||linkPropData.FlowchartMidpoint==0?linkPropData.FlowchartMidpoint:0,
                            }
                            break;
                        case "StateMachine":
                            connectorParam[1]={
                                curviness:linkPropData.StateMachineCurviness||linkPropData.StateMachineCurviness==0?linkPropData.StateMachineCurviness:10,
                                margin:linkPropData.StateMachineMargin||linkPropData.StateMachineMargin==0?linkPropData.StateMachineMargin:5,
                                proximityLimit:linkPropData.StateMachineProximityLimit||linkPropData.StateMachineProximityLimit==0?linkPropData.StateMachineProximityLimit:80,
                            }
                            break;
                    }
                    connection.setConnector(connectorParam);
                }
                let PaintStyle = _.cloneDeep(jsplumbConfig.ins.PaintStyle);
                if(linkPropData.PaintStyleStroke&&linkPropData.PaintStyleStroke.hex8){
                    PaintStyle.stroke = linkPropData.PaintStyleStroke.hex8;
                }
                if(linkPropData.PaintStyleStrokeWidth){
                    PaintStyle.strokeWidth = linkPropData.PaintStyleStrokeWidth;
                }
                connection.setPaintStyle(PaintStyle);
                let HoverPaintStyle = _.cloneDeep(jsplumbConfig.ins.HoverPaintStyle);
                if(linkPropData.HoverPaintStyleStroke&&linkPropData.HoverPaintStyleStroke.hex8){
                    HoverPaintStyle.stroke = linkPropData.HoverPaintStyleStroke.hex8;
                }
                if(linkPropData.HoverPaintStyleStrokeWidth){
                    HoverPaintStyle.strokeWidth = linkPropData.HoverPaintStyleStrokeWidth;
                }
                connection.setHoverPaintStyle(HoverPaintStyle);
                //
                connection.removeAllOverlays();
                linkPropData.overlayList&&linkPropData.overlayList.forEach(overlayItemData=>{
                    let overlayParam={id:overlayItemData.overlayId};
                    if(overlayItemData.location&&overlayItemData.location.split(",").length>1){
                        const oosp = overlayItemData.location.split(",");
                        overlayParam.location = [parseFloat(oosp[0]),parseFloat(oosp[1])]
                    }else if(overlayItemData.location){
                        overlayParam.location = parseFloat(overlayItemData.location);
                    }
                    overlayParam.direction = overlayItemData.direction||1;
                    switch (overlayItemData.type) {
                        case "Arrow":
                        case "PlainArrow":
                        case "Diamond":
                            if(overlayItemData.arrowSize){
                                const arrowSizeObject = overlayItemData.arrowSize.split(",");
                                if(arrowSizeObject.length>0){
                                    overlayParam.width = parseFloat(arrowSizeObject[0]);
                                    overlayParam.length = parseFloat(arrowSizeObject[0]);
                                }
                                if(arrowSizeObject.length>1){
                                    overlayParam.length = parseFloat(arrowSizeObject[1]);
                                }
                            }
                            break;
                        case "Label":
                            overlayParam.label = overlayItemData.labelText
                            let labelStyle = {};
                            if(overlayItemData.font){
                                labelStyle.font = overlayItemData.font;
                            }
                            if(overlayItemData.padding&&overlayItemData.padding.inputVal&&overlayItemData.padding.selectVal){
                                labelStyle.padding = overlayItemData.padding.inputVal+overlayItemData.padding.selectVal;
                            }
                            if(overlayItemData.borderWidth){
                                labelStyle.borderWidth = overlayItemData.borderWidth;
                            }
                            if(overlayItemData.fillStyle&&overlayItemData.fillStyle.hex8){
                                labelStyle.fill = overlayItemData.fillStyle.hex8;
                            }
                            if(overlayItemData.color&&overlayItemData.color.hex8){
                                labelStyle.color = overlayItemData.color.hex8;
                            }
                            if(overlayItemData.borderStyle&&overlayItemData.borderStyle.hex8){
                                labelStyle.borderStyle = overlayItemData.borderStyle.hex8;
                            }
                            overlayParam.labelStyle=labelStyle;
                            overlayParam.cssClass = "idm-overlay-label"
                            break;
                        case "Custom":
                            if(overlayItemData.contentFunction&&overlayItemData.contentFunction.length>0){
                                overlayParam.create=function(component){
                                    //
                                    //const d = document.createElement("span")
                                    //d.innerHTML = ""
                                    return that.commonCustomEventFunHandle(overlayItemData,"contentFunction",{component,overlaySetData:overlayItemData});
                                }
                            }else{
                                return;
                            }
                            break;
                    }
                    if(overlayItemData.clickFunction&&overlayItemData.clickFunction.length>0){
                        overlayParam.events={
                            click:(event, overlay) =>{
                                //
                                that.commonCustomEventFunHandle(overlayItemData,"clickFunction",{event,overlay,overlaySetData:overlayItemData});
                            }      
                        }  
                    }
                    connection.addOverlay([overlayItemData.type,overlayParam]);
                })
            }
        });
    },
    /**
      * 
      */
    reconnectionAllModule(){
        let that = this;
        let existsLink = this.plumbIns.select();
        that.PageSettingData.page.linkList.forEach(linkObject=>{
            let isExists = false;
            try {
                existsLink.each(connection=>{
                    if(connection.endpoints&&connection.endpoints.length>1){
                        if(connection.endpoints[0].anchor.x==linkObject.sourceEndpoint.x&&
                            connection.endpoints[0].anchor.y==linkObject.sourceEndpoint.y&&
                            connection.endpoints[1].anchor.x==linkObject.targetEndpoint.x&&
                            connection.endpoints[1].anchor.y==linkObject.targetEndpoint.y){
                                isExists = true;
                            }
                    }
                })
            } catch (error) {
                
            }
            if(!isExists){
                // sourceEndpoint
                let param = {
                    // source:linkObject.sourceId,
                    // target:linkObject.targetId,
                    source:that.getModuleEndpoint(linkObject.sourceId,linkObject.sourceEndpoint.x,linkObject.sourceEndpoint.y),
                    target:that.getModuleEndpoint(linkObject.targetId,linkObject.targetEndpoint.x,linkObject.targetEndpoint.y),
                    parameters:{
                        isreconnection:true,
                        linkObjectId:linkObject.id
                    }
                }
                that.plumbIns.connect(param);
                //
                that.linkConnectionStyleChange(linkObject);
            }
        })
    },
    /**
      * Endpoint
      * @param {*} packageid 
      * @param {*} x 
      * @param {*} y 
      * @returns 
      */
    getModuleEndpoint(packageid,x,y){
        let rtnEndpoint = null;
        this.plumbIns.selectEndpoints({ source: packageid }).each((endpoint) => {
            if(rtnEndpoint){
                return;
            }
            if(endpoint.anchor.x==x&&endpoint.anchor.y==y){
                rtnEndpoint = endpoint;
            }
        });
        return rtnEndpoint;
    },
    initJsPlumb(){
        let that = this;
        this.plumbIns = jsPlumb.getInstance(jsplumbConfig.ins)
        this.plumbIns.ready(function () {
            that.plumbIns.setContainer("idm_hover_layer_wrap")
        })
        this.plumbIns.bind('connection', function(conn, e) {
            let parameters = conn.connection.getParameters();
            if(parameters&&parameters.isreconnection){
                //id
                let filterList = that.PageSettingData.page.linkList.filter(item=>item.id==parameters.linkObjectId);
                if(filterList.length>0){
                    conn.connection.canvas.id=filterList[0].canvasId;
                    conn.connection.id = filterList[0].connId
                }
                return
            }
        });

    },
  },
};
</script>

